.modal__content {
  display: block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  width: 318px;
  margin: auto;
  background-color: #ffffff;
  box-shadow: 0 5px 10px 0 rgba(0, 1, 1, 0.25);
  border: solid 1px #c6c6c6;
  z-index: 11;

  @media (min-width: $tablet-width) {
    width: 566px;
  }
}

.modal__inner {
  @include inner;

  @media (min-width: $desktop-width) {
    width: auto;
  }
}

.modal__title {
  width: 160px;
  font-size: 25px;
  font-weight: bold;
  line-height: 1.2;
  text-transform: uppercase;
  padding: 36px 0 25px 0;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    width: auto;
  }
}

.modal__legend {
  text-align: center;
  font-size: 15px;
  line-height: 2;
  color: $color-danger;
  margin-bottom: 25px;

  @media (min-width: $tablet-width) {
    font-size: 17px;
    line-height: 1.76;
  }
}

.modal__sizes {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 30px;

  @media (min-width: $tablet-width) {
    width: 365px;
    margin: 0 auto 30px auto;
  }
}

.modal__input:checked + .modal__size {
  border: solid 2px #63d1bb;
}

.modal__size {
  font-size: 17px;
  font-weight: bold;
  line-height: 0.94;
  color: $color-danger;
  padding: 23px 34px 20px 34px;
  border: solid 2px #ececec;
  background-color: transparent;

  @media (min-width: $tablet-width) {
    padding: 20px 52px;
  }
}

.modal__btn {
  color: #ffffff;
  background-color: #63d1bb;
  border: none;
  margin-bottom: 46px;
  padding: 20px 78px;

  @include button-style;

  @media (min-width: $tablet-width) {
    padding: 20px 134px;
  }
}

.modal__fieldset {
  border: none;
  padding: 0;
}

.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.8);
}

.modal-add {
  display: block;
}
